import React from 'react';
import './Task.css';
import {Timer} from '../Timer';

function Task({id, task_date, task_client, task_description, onClick}) {
  return (
    <li onClick={onClick}  className="list-group-item list-group-item-action" style={{padding: '0px'}}>
      <div key={id} className="Task">
        <div className="Task-date" >{new Date(task_date).toLocaleDateString()}</div>
        <div className="Task-client" >{task_client}</div>
        <div className="Task-description" >{task_description}</div>
        <div className="Task-timer" >
          <Timer task_id={id}/>
        </div>
      </div>
    </li>
  );
}

export default Task;
