import React from 'react';
import { ClientTasks, GroupTask } from './Tasks';
import { FilterDate } from './calendar';
import './Page.css';
import AuthContext from './AuthContext';

function formatTwoNumber(number) {
  return (number > 9) ? number : '0'+number;
}

function formatDate(date) {
  var y = date.getFullYear();
  var m = date.getMonth()+1;
  var d = date.getDate();

  return `${y}-${formatTwoNumber(m)}-${formatTwoNumber(d)}`;
}

class PageTask extends React.Component {
  constructor(props) {
    super(props);

    var date = new Date();
    var firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
    var lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);

    this.state = {
      isLoadding: false,
      isError: false,
      clients: [],
      tasks: [],
      timers: [],
      filter: {
        task_parent: null,
        task_date:{
          from: formatDate(firstDay),
          to: formatDate(lastDay)
        }
      },
      modal: false,
      modalNew: false,
      formTask: {}
    }

    this.getTasks = this.getTasks.bind(this);
  }

  getTasks(state) {
    if(!state) {
      state = this.state;
    }

    return this.context.api.list('tasks', state.filter)
    .then(response => {
      return this.state.setState(() => ({
        ...state,
        tasks: response,
        isLoading: false,
        isError: false
      }));
    })
    .catch(err => 
      this.state.setState(() => ({...state, isError: true, isLoading: false}))
    )
  }

  render() {
    console.log(this.state)
    return (
      <div className="App">
        <div className="Page">
          <div className="Header">
            <nav className="navbar navbar-light bg-light">
              <a className="navbar-brand" href="/">Time Tracker</a>
              <div className="btn-group">
                <button className="btn btn-outline-success"
                  type="button"
                  onClick={() => this.setState({
                    modal: true,
                    modalNew: true,
                    formTask: {task_parent: this.state.filter.task_parent}
                  })}
                >New Task</button>
                <button className="btn btn-outline-success"
                  type="button"
                  onClick={() => {
                    this.context.api.exportExcel('tasks', this.state.filter)
                    .then(console.log)
                    .catch(console.log)
                  }}
                >Export excel</button>
                <button className="btn btn-outline-danger"
                  type="button"
                  onClick={() => this.context.signOff()}
                >Sign out</button>
              </div>
            </nav>
          </div>
          <div className="Nav">
            <FilterDate 
              onChange={task_date => 
                this.getTasks({...this.state, filter:{...this.state.filter, task_date}})}
            />
            <GroupTask
              value={this.state.filter.task_parent}
              onChange={task_parent => 
                this.getTasks({...this.state, filter:{...this.state.filter, task_parent}})}
            />
          </div>
          <div className="Article">
            <ClientTasks {...this.state}
              setState={(f) => this.setState(f)}
              onCloseModal={() => this.setState({
                modal: false,
              })}
            />
          </div>
        </div>
      </div>
    );
  }
}

PageTask.contextType = AuthContext;

export default PageTask;
